        return fetch('https://us-west-2.aws.webhooks.mongodb-realm.com/api/client/v2.0/app/nupy-cfbnr/service/restaurant/incoming_webhook/web_guardarPedido', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(boleta)
        })
        .then(res => res.json())
        .then(res => {
            console.log(res)
            return res
        })
        .catch(error => {
            console.log(error)
            return error
        })
